 *
 * Replace them with your own styles or remove the file completely.
 */
.wptravel-blocks {
    &.trip-fixed-departure {
        .fixed-date-dropdown {
            position: relative;
            display: inline-block;

            .dropbtn {
                color: #555;
                border: none;
                cursor: pointer;
                display: flex;
                align-items: center;
            }

            .dropbtn::after {
                content: "ï„‡";
                font-family: "Font Awesome 5 Free";
                font-weight: 900;
                margin-left: 5px;
                color: #555;
                transition: all .2s ease;
                transform: rotate(-90deg);
            }

            .dropdown-content {
                visibility: hidden;
                opacity: 0;
                position: absolute;
                background-color: #FFFFFF;
                min-width: 200px;
                box-shadow: 0px 2px 10px 0px rgba(0,0,0,.1);
                z-index: 3;
                display: grid;
                grid-template-columns: repeat(auto-fit, minmax(165px, 1fr));
                transition: all .2s ease-in-out;

                .dropdown-list {
                    color: #555;
                    padding: 12px 16px;
                    font-weight: 400 !important;
                    position: relative;
                    text-decoration: none;
                    display: block;
                    transition: all .2s ease;
                    z-index: 1;
                    cursor: pointer;
                    margin-left: 0;

                    &:before {
                        content: none;
                        background: #838383;
                        width: 3px;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        left: 0;
                        transition: all .3s ease;
                    }

                    &:nth-child(odd) {
                        border-right: 1px solid #ccc;
                    }

                    &:hover {
                        background: #EEE;
                        color: #282828;
                        transition: all .3s ease;
                    }
                }
            }

            &:hover { 
                .dropbtn::after {
                    transform: rotate(0deg);
                }

                .dropdown-content {
                    visibility: visible;
                    opacity: 1;
                    transition: all .2s ease;

                    .dropdown-list:hover::before {
                        transition: all .3s ease;
                        content: none;
                    }
                }
            }
        }
    }
}